////
/// Nav menu component.
///
/// @group  Components
/// @author Steffen Bang Nielsen <sbn@retrofitter.dk>
/// @link   https://retrofitter.dk
////

.menu {

	// Specific nav menus.
	&--primary {
		position: absolute;
		overflow: hidden;
		top:-100%;
		right: 0;
		left: 0;
		width: 100%;
		height: 0%;
		transition: left 0.3s ease;
		z-index: 999;
		background: var(--color-white);
		padding-bottom: 2rem;
		&[aria-expanded="true"],
		&:target {
			position: fixed;
			overflow-y: scroll;
			top: 6rem;
			height: auto;
			max-height: 75%;
			box-shadow: 0.25rem 1rem 1rem rgba(0,0,0,.25);
			+ .backdrop {
				position: fixed;
				display: block;
				content: "";
				left: 0;
				top: 6rem;
				width: 100%;
				height: 100%;
				z-index: 998;
				background: #000;
				background: rgba(0,0,0,.75);
				cursor: default;
				.has-custom-logo & {
					top: 12rem;
				}
				.logocenter & {
					top: 12rem;
					@media ( max-width: break( medium ) ) {
						top: 10rem;
					}
				}
				.hide-site-description.logocenter & {
					top: 8rem;
				}
				.has-custom-logo.logocenter & {
					top: 18rem;
					@media ( max-width: break( medium ) ) {
						top: 14rem;
					}
				}
				.has-custom-logo.hide-site-description.logocenter & {
					top: 14rem;
				}
			}
			.has-custom-logo & {
				top: 10rem;
			}
			.logocenter & {
				top: 10rem;
				@media ( max-width: break( medium ) ) {
					top: 8rem;
				}
			}
			.hide-site-description.logocenter & {
				top: 8rem;
			}
			.has-custom-logo.logocenter & {
				top: 18rem;
				@media ( max-width: break( medium ) ) {
					top: 14rem;
				}
			}
			.has-custom-logo.hide-site-description.logocenter & {
				top: 14rem;
			}
		}
		.menu__link {
			color: var(--color-black);
			&:visited,
			&:hover,
			&:focus,
			&:active {
				color: var(--color-black);
			}
		}
		.menu__item {
			margin: 0;
			line-height: 2.2rem;
			> ul {
				margin:0;
				font-size: 100%
			}
		}
		.menu__items {
			display: flex;
			// flex-wrap: wrap;
			justify-content: space-evenly;
			> li {
				margin-top: 1rem;
				// width: 100%;
				> a {
					font-weight: bolder;
					text-transform: uppercase;
				}
				&:before {
					content: "";
					position: absolute;
					width: 40px;
				}
			}
		}
	}
	&--featured {
		font-family: var(--font-secondary);
		// line-height: 3rem;
		overflow: hidden;
		display: none;
		margin-right: 1rem;
		@media ( min-width: break( small ) ) {
			display: inline-flex;
			flex: 0 2 auto;
		}
		.logoright & {
			order: 2;
		}

		.menu__link {
			color: var(--color-primary);
			&:visited,
			&:hover,
			&:focus,
			&:active {
				color: var(--color-primary);
			}
		}
		.menu__items {
			display: flex;
			flex-wrap: wrap;
			justify-content: flex-end;
			.logoright & {
				justify-content: flex-start;
			}
		}
		.menu__item {
			display: inline-block;
			padding: 0 0 0 1rem;
			.logoright & {
				padding: 0 1rem 0 0;
			}
		}
	}
	&--social {
		.menu__items {
			display: flex;
			justify-content: space-between;
			max-width: 16rem;
		}
		.menu__link {
			@include font-size(32);
			// font-size: var(--fontsize-regular);
		}
		a {
			&:before {
				content: "*";
				font-family: "Finicon";
				vertical-align: middle;
			}
			&[href*="linkedin.com"],
			&[href*="linkedin.dk"] {
				&:before {
					content: "L";
				}
			}
			&[href*="facebook.com"],
			&[href*="facebook.dk"] {
				&:before {
					content: "F";
				}
			}
			&[href*="instagram.com"],
			&[href*="instagram.dk"] {
				&:before {
					content: "I";
				}
			}
			&[href*="twitter.com"],
			&[href*="twitter.dk"] {
				&:before {
					content: "T";
				}
			}
			&[href*="pinterest.com"],
			&[href*="pinterest.dk"] {
				&:before {
					content: "P";
				}
			}
			&[href*="mailto:"] {
				&:before {
					content: "a";
				}
			}
		}
	}


	// Menu title.
	&__title {}

	// Menu items.
	&__items {
		list-style: none;
		margin: 0;
		padding-left: 0;
	}

	// Individual items.
	&__item {}

	// Item link.
	&__link {
		text-decoration: none;
		@include font-size(20);
		color: var(--color-primary);
	}
}
